.mat-toolbar {
    display: flex;
    justify-content: space-between;
    /* background-color: rgba(135, 148, 162, 0.73); */
    background-color: rgb(178, 189, 202);
    background: linear-gradient(135deg, rgb(206, 213, 221), rgb(169, 178, 187));
    padding: 0 16px;
}

.toolbar-content {
    display: flex;
    align-items: center;
}

.logo-container {
    display: flex;
    align-items: center;
    margin-right: 16px;
}

.logo {
    height: 90px;
    width: 110px;
}


