.buttonLog {
    height: 40px;
    border-radius: 20px;
    font-size: 0.875rem;
    font-weight: 600;
    text-align: center;
    color: black;
    padding: 0 16px;
    background-color: white;
    transition: background-color 0.3s, transform 0.3s;
    margin-right: 16px;
}

.buttonLog:hover {
    background-color: #d4e6f1;
    transform: scale(1.05);
}

.welcome-message {
    display: flex;
    align-items: center;
    font-size: 1.25rem;
    font-weight: 500;
    color: white;
    opacity: 0;
    animation: fadeIn 1s forwards;
}

mat-toolbar {
    display: flex;
    height: 13vh;
    justify-content: space-between;
}

mat-icon {
    margin-right: 8px;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}
